@keyframes fadeIn {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}

.page-header {
  position: relative;
  width: 100%;
  height: 90px;
  padding-top: 16px;
  background-color: #fff;
  z-index: 1000;
}

@media (min-width: 768px) {
  .page-header {
    margin-top: 30px;
  }
}

.page-header--sticky {
  position: fixed;
  top: 0;
  margin-top: 0;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
}

.page-header .row {
  justify-content: space-between;
}

.header-logo {
  margin: 0 15px;
  width: 160px;
  height: auto;
}

@media (min-width: 1200px) {
  .header-logo {
    margin-left: 20px;
  }
}

.nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 15px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #6f42c1;
  box-shadow: 1px 2px 10px 1px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

@media (min-width: 1200px) {
  .nav {
    margin-right: 20px;
  }
}

.nav:hover {
  box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.2);
}

.nav-hamburger {
  width: 30px;
  height: 21px;
  z-index: 1000;
}

.nav-hamburger span {
  display: block;
  width: 30px;
  height: 3px;
  background-color: #fff;
  opacity: 1;
}

.nav-hamburger > * + * {
  margin-top: 6px;
}

.nav-hamburger--check span:first-child {
  margin: 0;
  transform: rotate(45deg);
  transform-origin: 4px 6px;
  animation: fadeIn 0.3s ease-in-out;
  transition: transform 0.3s;
}

.nav-hamburger--check span:last-child {
  margin: 0;
  transform: rotate(-45deg);
  transform-origin: 22px 5px;
  animation: fadeIn 0.3s;
  transition: transform 0.3s;
}

.nav-hamburger--check span:nth-child(2) {
  display: none;
  opacity: 0;
}

.nav-list {
  position: absolute;
  top: 100%;
  right: 0;
  padding-top: 20px;
  padding-bottom: 30px;
  width: 100vw;
  background-color: #f5f5f5;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
  z-index: -5;
}

.nav-item {
  position: relative;
  display: block;
  padding: 16px 12px 16px 12px;
  font-size: 1rem;
  color: #696969;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.nav-item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 10px;
  transform: translateX(-50%);
  height: 2px;
  background-color: #696969;
  transition: width 0.3s;
}

.nav-item:hover::after {
  width: 100px;
}

.page-footer {
  min-height: 160px;
  padding-top: 40px;
  padding-bottom: 50px;
  background-color: #2f165d;
  background-color: #3D383E;
}

.page-footer .row {
  justify-content: space-between;
}

.footer-module {
  text-align: center;
}

.footer-module > *:not(:last-child) {
  margin-bottom: 20px;
}

.footer-logo {
  text-align: center;
}

.footer-logo a {
  position: relative;
  padding-bottom: 3px;
  font-size: 1rem;
  color: #a2a2a2;
